import logo from './logo.svg';
import './App.css';
import { Progress, QRCode } from 'antd';
import { useEffect, useState } from 'react';

function App() {

  const [qr, setQr] = useState('myQrCode');
  const [timer, setTimer] = useState<number>(0)

  useEffect(() => {
    const interval = setInterval(() => {
      if(timer < 20) {
        setTimer(timer + 0.05); 
      }
    }, 50)

    return () => clearInterval(interval);
  }, [timer]);


  return (
    <div className="App">
      <header className="App-header">
        <img src={logo} className="App-logo" alt="logo" />
        <p>
          WhatsHack is turning on...
        </p>
        <QRCode
          errorLevel="H"
          size={400}
          iconSize={300/4}
          value="Mon QR code"
          color='#61DAFB'
          bgColor='#282C34'
          status='active'
        />
        <Progress percent={100/20*timer} size={[380, 5]} strokeColor='#61DAFB' showInfo={false}  />
      </header>
    </div>
  );
}

export default App;
